<!--上传中心-->
<template>
	<div class="upload-center">
		<uploadHeader></uploadHeader>
		<div class="container">
			<div class="container-head">
				<ul>
					<li @click="handleChange('uploading')" :class="{active:active==='uploading'}">上传中</li>
					<li @click="handleChange('uploaded')" :class="{active:active==='uploaded'}">待发布</li>
					<li @click="handleChange('published')" :class="{active:active==='published'}">已发布</li>
				</ul>
				<template v-if="active==='published'">
					<div class="head-filter">审核状态<i class="el-icon-arrow-down"></i></div>
					<div class="head-filter">发布去向<i class="el-icon-arrow-down"></i></div>
				</template>
				<el-upload class="upload-demo" ref="upload" action="http://192.168.38.221:8081/uploadFile/uploadFile.do" :file-list="fileList"
				 :on-progress="uploadProgress" :on-success="uploadSuccess" :on-change="uploadChange" :headers="tokenData" :data="type">
					<el-button slot="trigger" size="small" type="primary">
						<i class="iconfont icon-add"></i>
						上传
					</el-button>
				</el-upload>
			</div>
			<div class="container-main">
				<template v-if="active === 'uploading'">
					<el-table :data="uploadingData" key="uploadingData">
						<el-table-column type="index" align="center" width="60" label="序号"></el-table-column>
						<el-table-column prop="name" label="文件名">
							<template slot-scope="scope">
								<div class="text-overflow">{{scope.row.name}}</div>
							</template>
						</el-table-column>
						<el-table-column width="100" align="center" prop="size" label="文件大小">
							<template slot-scope="scope">
								<div class="text-overflow">{{fileSize(scope.row.size)}}</div>
							</template>
						</el-table-column>
						<el-table-column prop="status" align="center" label="状态">
							<template slot-scope="scope">
								<el-progress style="width:50%;margin:0 auto" :percentage="scope.row.status"></el-progress>
							</template>
						</el-table-column>
						<el-table-column width="60" label="操作">
							<template slot-scope="scope">
								<i style="cursor:pointer;" class="iconfont iconshanchu" @click="stopUpload(scope.row.file,scope.$index)"></i>
							</template>
						</el-table-column>
					</el-table>
				</template>
				<!-- 待发布 -->
				<template v-if="active === 'uploaded'">
					<el-table :data="uploadedData" key="uploaded">
						<el-table-column type="index" align="center" width="60" label="序号"></el-table-column>
						<el-table-column prop="fileName" label="文件名">
							<template slot-scope="scope">
								<div class="text-overflow">{{scope.row.fileName}}</div>
							</template>
						</el-table-column>
						<el-table-column width="100" align="center" prop="fileSize" label="文件大小">
							<template slot-scope="scope">
								<div class="text-overflow">{{fileSize(scope.row.fileSize)}}</div>
							</template>
						</el-table-column>
						<el-table-column prop="createtime" align="center" label="上传时间">
							<template slot-scope="scope">
								<div class="text-overflow">{{timeFormat(scope.row.createtime)}}</div>
							</template>
						</el-table-column>
						<el-table-column prop="fileTransStatus" align="center" label="状态">
							<template slot-scope="scope">
								<div class="text-overflow">{{scope.row.fileTransStatus}}</div>
							</template>
						</el-table-column>
						<el-table-column width="60" label="操作">
							<template slot-scope="scope">
								<i @mouseenter="scope.row.checked=true" v-if="!scope.row.checked" class="p-center iconfont icon-more"></i>
								<div @mouseleave="scope.row.checked=false" v-else class="control-btns">
									<span @click="jumpRes(scope.row.fileId)">发布</span>
									<span @click="issueDel(scope.row.fileId)">删除</span>
									<span>剪辑</span>
								</div>
							</template>
						</el-table-column>
					</el-table>
					<resPagination :total="totalDatas" @onSizeChange="jump"></resPagination>
				</template>
				<!-- 已发布 -->
				<template v-if="active === 'published'">
					<el-table :data="publishedData" key="published">
						<el-table-column type="index" align="center" width="60" label="序号"></el-table-column>
						<el-table-column prop="fileName" label="文件名">
							<template slot-scope="scope">
								<div class="text-overflow">{{scope.row.fileName}}</div>
							</template>
						</el-table-column>
						<el-table-column width="100" align="center" prop="fileSize" label="文件大小">
							<template slot-scope="scope">
								<div class="text-overflow">{{fileSize(scope.row.fileSize)}}</div>
							</template>
						</el-table-column>
						<el-table-column prop="createtime" align="center" label="上传时间">
							<template slot-scope="scope">
								<div class="text-overflow">{{timeFormat(scope.row.createtime)}}</div>
							</template>
						</el-table-column>
						<el-table-column prop="status" align="center" label="状态"></el-table-column>
						<el-table-column prop="publishWay" align="center" label="发布去向">
							<template slot-scope="scope">
								<div class="text-overflow">{{scope.row.publishWay == 10010?'资源平台':'同步课堂'}}</div>
							</template>
						</el-table-column>
						<el-table-column width="60" label="操作">
							<template slot-scope="scope">
								<i @mouseenter="scope.row.checked=true" v-if="!scope.row.checked" class="p-center iconfont icon-more"></i>
								<div @mouseleave="scope.row.checked=false" v-else class="control-btns">
									<!-- <span :id="scope.row.fileId">编辑</span> -->
									<span @click="issueDel(scope.row.fileId)">删除</span>
									<span @click="jumpEdit(scope.row.fileId)">标记</span>
								</div>
							</template>
						</el-table-column>
					</el-table>
					<resPagination :total="totalDatas1" @onSizeChange="jump1"></resPagination>
				</template>
			</div>
		</div>


		<!-- 请选择发布平台  -->
		<!-- <div class="pub-platform">
      helloworld
    </div> -->
	</div>
</template>

<script>
export default {
    components: {
        uploadHeader: () => import("@/components/upload-head"),
        resPagination: () => import("@/components/res-pagination")
    },
    data() {
        return {
            tokenData: {
                'Authorization': localStorage.getItem('Authorization')
            },
            type: {
                'upType': '10000'
            },
            active: 'uploading',
            fileList: [],
            uploadingData: [],
            uploadedData: [{
                order: 1,
                time: "2019-05-25  10:22",
                name: "殖民地人民的反抗斗争-历史九年级下册.mp4",
                size: "400MB",
                status: "转码中",
                checked: false
            }],
            publishedData: [{
                order: 1,
                time: "2019-05-25  10:22",
                name: "殖民地人民的反抗斗争-历史九年级下册.mp4",
                size: "400MB",
                status: "转码中",
                dire: '同步课堂',
                checked: false
            }],
            totalDatas:0,
            pageNo:1,
			totalDatas1:0,
			pageNo1:1
        };
    },
    created() {
        this.waitIssue()
        this.Issueed()
    },
    mounted() {
        //  this.$myLayer.formLayer("",$(".pub-platform"),["360px",'225px'])
    },
    methods: {
        handleChange(type) {
            this.active = type;
        },
        uploadProgress(event, file, fileList) {
            let data = []
            let item;
            for (let i in fileList) {
				if(fileList[i].percentage != 100){
					item = {
					    name: fileList[i].name,
					    size: fileList[i].size,
					    status: Math.ceil(fileList[i].percentage),
					    file: fileList[i]
					}
					data.push(item)
				}
            }
            this.uploadingData = data;
        },
        uploadSuccess(response, file, fileList) {
            if (response.success) {
                this.$myLayer.successLayer(response.msg)
                this.waitIssue()
            }
			let data = []
			let item;
			for (let i in fileList) {
				if(fileList[i].percentage != 100){
					item = {
					    name: fileList[i].name,
					    size: fileList[i].size,
					    status: Math.ceil(fileList[i].percentage),
					    file: fileList[i]
					}
					data.push(item)
				}
			}
			this.uploadingData = data;
        },
        stopUpload(file,index) {
            this.$refs.upload.abort(file);
			this.uploadingData.splice(index,1)
        },
        uploadChange(file, fileList){
			console.log(fileList)
        },
        uploadFile(param) {
            let fileObj = param.file;
        },
        waitIssue() {
            this.$api.issueList({
                isdeleted: 1,
                pageNo: this.pageNo,
                pageSize: 10,
                upStatus: 9081,
                publishStatus: 9091
            }).then(res => {
                this.totalDatas = res.totalDatas
                res.data.map(v => {
                    v.checked = false
                })
				console.log(123)
                this.uploadedData = res.data;
            })
        },
        jump(val){
			this.pageNo = val;
            this.waitIssue()
        },
		jump1(val){
			this.pageNo1 = val;
			this.Issueed()
		},
        Issueed() {
            this.$api.issueList({
                isdeleted: 1,
                pageNo: this.pageNo1,
                pageSize: 10,
                upStatus: 9081,
                publishStatus: 9090
            }).then(res => {
                this.totalDatas1 = res.totalDatas
                res.data.map(v => {
                    v.checked = false
                })
				console.log(456)
                this.publishedData = res.data;
            })
        },
        issueDel(id) {
            this.$api.issueDel({
                fileId: id,
                isdeleted: 0
            }).then(res => {
                if (res.success) {
                    this.$myLayer.successLayer(res.msg)
                } else {
                    this.$myLayer.errorLayer(res.msg)
                }
                this.waitIssue();
                this.Issueed();
            })
        },
		jumpRes(id) {
		    this.$router.push({
				path: `/resource/Layout/resource-list/upload/${id}`
			})
		},
		jumpEdit(id){
			this.$router.push({
				path: `/resource/Layout/resource-list/edit/${id}`
			})
		},
        fileSize(size) {
            let value;
            let param = parseInt(size)
            if (param >= 1024 * 1024 * 1024) {
                value = (param / (1024 * 1024 * 1024)).toFixed(2) + "GB"
            } else {
                if (param >= 1024 * 1024) {
                    value = (param / (1024 * 1024)).toFixed(2) + "MB"
                } else {
                    if (param >= 1024) {
                        value = (param / 1024).toFixed(2) + "kB"
                    } else {
                        value = param + "B"
                    }
                }
            }
            return value
        },
        timeFormat(time) {
            let value = new Date(time);
            let year = value.getFullYear();
            let month = value.getMonth() + 1 <= 9 ? "0" + (value.getMonth() + 1) : value.getMonth() + 1;
            let day = value.getDate() <= 9 ? "0" + value.getDate() : value.getDate();
            let hour = value.getHours() <= 9 ? "0" + value.getHours() : value.getHours();
            let min = value.getMinutes() <= 9 ? "0" + value.getMinutes() : value.getMinutes();
            return `${year}-${month}-${day} ${hour}:${min}`
        }
    }
};
</script>
<style>
	.container-head .el-upload-list.el-upload-list--text {
		display: none;
	}
</style>

<style lang="scss" scoped>
	.container {
		width: 1200px;
		margin: 0 auto;
		padding-top: 32px;
	}

	.container-head {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.container-head ul {
		flex: 1;
	}

	.container-head ul li {
		display: inline-block;
		margin-right: 40px;
		font-size: 16px;
		cursor: pointer;
		border-bottom: 2px solid transparent;
		padding-bottom: 2px;

		&.active {
			color: #1d71f5;
			border-color: #1d71f5;
		}
	}

	.head-filter {
		margin-right: 40px;
		cursor: pointer;
	}

	.container-main {
		margin-top: 20px;
	}

	.control-btns {
		position: absolute;
		right: 0;
		top: 0;
		bottom: 0;
		background: #E3EBF9;
		display: flex;
		align-items: center;

		span {
			display: inline-block;
			width: 50px;
			text-align: center;
			cursor: pointer;

			&:hover {
				color: #1D71F5;
			}
		}
	}
</style>
<style lang="scss">
	.upload-center .el-button--primary.el-button span {
		color: #fff;

		i {
			color: #fff;
		}
	}
</style>
